body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  flex-direction: column;
  height: 100%;
}
.puzzle {
    background-color: gray;
    width: 220px;
    height: 222px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin: 0px auto
}

.btn-container {
    display: flex;
    justify-content: center;
    margin-top: 10px;
}

.btn-container button {
    padding: 5px 5px;
    width: 100px;
}

.container {
    width: 100%;
    margin-top: 50px;
}

.footer {
    position: fixed;
    bottom: 0px;
    text-align: center;
    width: 100%;
    margin-bottom: 5px;
}
.box {
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px solid white;
    border-radius: 3px;
    background-color: black;
    color: white;
    cursor: pointer;
}
